<template>
  <div class='user'>
    <div class='user-avatar'>
      <img
        :src='`https://api.adorable.io/avatars/75/${user.avatar}`'
        :alt='`${user.username} avatar`'
      />
    </div>
    <div class='user-info'>
      <div class='user-info-name'>{{user.username}}</div>
      <div class='user-info-stats'>
        <p>Rank: {{user.rank}}</p>
        <p>Ratio: {{user.wins}} - {{user.losses}} ({{user.streak}})</p>
      </div>
    </div>

    <div class='user-charlist'>
      <img v-for='(char, key) in charList' :key='key' :src='`/img/game/${char.facepic}.jpg`' />
    </div>
  </div>
</template>

<script>
export default {
  props: ["user"],
  computed: {
    charList() {
      const arr = [];
      for (const char of this.$store.getters["game/getMyCharList"](
        this.user.id
      )) {
        if (char) arr.push(char);
      }
      return arr;
    },
  },
};
</script>

