<template>
  <div class='ingame'>
    <section class='ingame-header'>
      <!-- PLAYER 1 view -->
      <div class='ingame-player' @click="selectUserView(GetMyData)">
        <img
          class='ingame-player-icon'
          :src='`https://api.adorable.io/avatars/75/${GetMyData.avatar}`'
          alt
        />
      </div>

      <!-- MIDDLE CONTROL PANEL -->
      <div class='ingame-control'>
        <div class='ingame-control-timebar'>
          <div class='ingame-control-timebar-gauge' :style='`width: ${timer}%`'></div>
        </div>
        <button class='ingame-control-btn' v-if='GetMyData.isTurn' @click='endTurn()'>
          <span>end turn</span>
        </button>
        <ul class='ingame-control-energypool' v-if='GetMyData.isTurn'>
          <li
            v-for='(amount, key) in GetMyData.energyPool.slice(0,4)'
            :key='key'
            :class='`${energies[key]} ${energies[key]}-glow`'
          >{{amount}}</li>
        </ul>
      </div>

      <!-- PLAYER 2 view -->
      <div class='ingame-player ingame-player-2' @click="selectUserView(myEnemyData)">
        <img
          class='ingame-player-icon'
          :src='`https://api.adorable.io/avatars/75/${myEnemyData.avatar}`'
          alt
        />
      </div>
    </section>

    <section class='ingame-field'>
      <MyTeam></MyTeam>
      <EnemyTeam></EnemyTeam>
    </section>

    <section class='ingame-footer'>
      <Viewer></Viewer>
    </section>
    <!-- MODALS -->
    <EnergyCart
      @endturn='endTurn'
      v-if='endturn && GetMyData.isTurn'
      :queue='GetMyData.payupCart'
      @close='endturn=false'
    />
  </div>
</template>

<script>
import components from "./script/components";
import computed from "./script/computed";
import methods from "./script/methods";
import watch from "./script/watchers";

export default {
  data() {
    return {
      energies: ["speed", "strength", "elemental", "wisdom"],
      endturn: false,
      timer: 100,
      tick: null,
      theme: {},
    };
  },
  components,
  computed,
  methods,
  watch,
  created() {
    const room = this.$store.getters["game/getBattleRoom"];
    if (room !== null) {
      room.onMessage("start-new-turn", (p) => {
        this.$store.dispatch("audio/playTurnSound");
        clearInterval(this.tick);
        this.timer = 100;
        this.tick = setInterval(() => {
          this.timer = this.timer - 1.66;
          if (this.timer === 0) {
            clearInterval(this.tick);
          }
        }, 1000);

        this.$store.commit("game/setGameState", p);
        if (this.GetMyData.isTurn)
          this.$store.commit("midori/setUpdate", {
            value: true,
            index: 0,
          });
        else {
          this.$store.commit("midori/setUpdate", {
            value: true,
            index: 1,
          });
        }
      });

      room.onMessage("update-temp-queue", (q) => {
        this.$store.commit("game/setTempQueue", q);
        this.$store.commit("game/setPlayerEnergyPool", {
          playerIndex: q.playerIndex,
          energyPool: q.energyPool,
        });
        this.$store.commit("game/setPayupCart", {
          p: q.playerIndex,
          pay: q.payupCart,
        });
      });

      room.onMessage("end-game", (/*finalData*/) => {
        clearInterval(this.tick);
      });
    }

    this.tick = setInterval(() => {
      this.timer = this.timer - 1.66;
      if (this.timer === 0) {
        clearInterval(this.tick);
      }
    }, 1000);

    const music = this.$store.getters["audio/getHomeMusic"];
    music.sound.volume = 0.07;
    music.loop = true;
    setTimeout(() => {
      music.sound.play();
    }, 1700);
    this.theme = music;
  },
  mounted() {
    if (this.GetMyData.isTurn)
      this.$store.commit("midori/setUpdate", {
        value: true,
        index: 0,
      });
    else
      this.$store.commit("midori/setUpdate", {
        value: true,
        index: 1,
      });

    this.selectUserView(this.myEnemyData)
  },
  beforeDestroy() {
    this.$store.commit("game/setRoom", null);
  },
};
</script>

<style lang="scss">
@import "./style";
</style>