const endTurn = function () {
    if (this.tempSkillQueue.length > 0) {
        this.endturn = true
    } else {
        const room = this.$store.getters['game/getBattleRoom']
        room.send('end-game-turn')
    }
}

const selectUserView = function(user){
    this.$eventBus.$emit("selected-an-user", user)
}

export default {
    endTurn,
    selectUserView
}