const myTeam = function () {
    return this.$store.getters["game/getMyCharList"](this.myId)
}

const myData = function () {
    return this.$store.getters['game/GetMyData'](this.myId)
}

const tempQueue = function () {
    return this.$store.getters['game/getTempQueueByCharacter']
}

const myId = function(){
    return this.$store.state.user.id
}

const skill = function () {
    return this.$store.getters["game/getSkillByCaster"];
}

const skillQueue =  function(){
  return this.$store.getters['game/getSkillQueueById'];
}

export default {
    myId,
    skill,
    myTeam,
    myData,
    skillQueue,
    tempQueue
}