<template>
  <section class='enemy-char' v-if='char!==false'>
    <div class='enemy-char-status'>
      <div class='enemy-icon'>
        <img
          @click='selectTarget()'
          v-if='isTarget.isTarget'
          class='pulsate'
          :src='`/img/game/${char.facepic}.jpg`'
        />
        <img @click="displayChar()" :src='`/img/game/${char.facepic}.jpg`' :class='{"fainted": char.knockedOut}' />
      </div>

      <div class='enemy-hp'>
        <div class='enemy-hp-value'>{{char.hitPoints}}</div>
        <div class='enemy-hp-gauge' :style='`width: ${char.hitPoints}%`'></div>
      </div>
    </div>
    <v-popover
      class='enemy-effect'
      trigger='hover'
      v-for='(s, key) in skillQueue(index, playerId).reverse()'
      :key='key'
    >
      <img :src='`/img/game/${s.skillpic}.jpg`' />
      <template slot='popover'>
        <ul>
          <li>{{s.skillName}}</li>
          <li v-for='(message, key) in s.tips' :key='key'>
            <ul>
              <li>{{message.message}}</li>
              <li>{{Math.floor(message.duration)}} turns left</li>
            </ul>
          </li>
          <li v-for='(message, key) in s.notifications' :key='key'>
            <ul>
              <li>{{message}}</li>
            </ul>
          </li>
        </ul>
      </template>
    </v-popover>

    <div class='enemy-effect'>
      <img
        v-for='(s, key) in tempQueue(index).reverse()'
        @dblclick='removeFromTempQueue(s)'
        :key='key'
        :src='`/img/game/${skill(s.caster, s.skill).skillpic}.jpg`'
      />
    </div>
  </section>
</template>

<script>
export default {
  props: ["char", "index"],
  methods: {
    selectTarget() {
      // emit received by MyTeam/index
      this.$eventBus.$emit("set-target", this.index);
      this.$store.commit("game/setSkillInFocus", {});
      this.$store.commit("game/resetTargetables");
    },
    removeFromTempQueue(s) {
      const room = this.$store.getters["game/getBattleRoom"];
      room.send("remove-skill-from-queue", s);
    },
    displayChar(){
      this.$eventBus.$emit("selected-a-char", this.char)
    }
  },
  computed: {
    isTarget() {
      return this.$store.getters["game/getCharacterByIndex"](this.index);
    },
    tempQueue() {
      return this.$store.getters["game/getTempQueueByCharacter"];
    },
    skill() {
      return this.$store.getters["game/getSkillByCaster"];
    },
    skillQueue() {
      return this.$store.getters["game/getSkillQueueById"];
    },
    playerId() {
      return this.$store.getters["user/getUserData"].id;
    },
  },
};
</script>

<style lang="scss">
.tempSkill {
  width: 30px;
  border-radius: 50%;
  margin: 1px;
}
</style>