<template>
  <section class="skill">
    <img class='skill-img' :src='`/img/game/${data.banner}.jpg`' />
    <div class='skill-info'>
      <div class="skill-info-small">
        <p class="skill-name">{{data.name}}</p>
        <p class="skill-cost">
          Cost: <span v-for='(c, k) in costs' :key='k' :class='`viewer-energy ${c}`'></span>
        </p>
      </div>
      <div class="skill-info-large">
        {{data.description}}
      </div>
      <div class="skill-info-small">
        <p class="skill-classes">
          Classes: <span :class="`${types[data.type[0]]}--text`">{{types[data.type[0]]}}</span>, {{classes[data.class]}}
        </p>        
        <p class="skill-cooldown">Cooldown: {{data.baseCooldown}}</p>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: ["data"],
  computed: {
    costs() {
      const energies = ["speed", "strength", "elemental", "wisdom", "random"];
      let cost = [];
      for (let i = 0; i < energies.length; i++) {
        for (let key = 0; key < this.data.cost[i]; key++) {
          cost.push(energies[i]);
        }
      }
      return cost;
    },
    types(){
      return this.$store.getters['enums/pokemonTypes']
    },
    classes() {
      return this.$store.getters['enums/skillClassTypes']
    }
  },
};
</script>


