<template>
  <div class='charface-status'>
    <div class='charface-icons'>
      <!--<img class="pulsate" :src='`/img/game/${char.facepic}.jpg`' /> -->
      <img
        @click='selectTarget()'
        v-if='char.isTarget'
        class='pulsate'
        :src='`/img/game/${char.facepic}.jpg`'
      />
      <img @click="displayCharInfo" :src='`/img/game/${char.facepic}.jpg`' :class='{"fainted": char.knockedOut}' />
    </div>
    <div class='charface-hp'>
      <div class='charface-hp-value'>{{char.hitPoints}}</div>
      <div class='charface-hp-gauge' :style='`width: ${char.hitPoints}%`'></div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["char", "index"],
  methods: {
    selectTarget() {
      // emit received by MyTeam/index
      this.$eventBus.$emit("set-target", this.index);
      this.$store.commit("game/setSkillInFocus", {});
      this.$store.commit("game/resetTargetables");
    },

    displayCharInfo(){
      this.$eventBus.$emit("selected-a-char", this.char)
    }
  },
};
</script>

