        position: relative;
        border-radius: 15px;
        overflow: hidden;
        margin-top: 5px;
        border: 2px solid #fafafa;
        box-shadow: 1px 1px 2px 2px rgba($color: #000000, $alpha: 0.2);

        &-value {
            position: absolute;
